    <script>
    import {
        defineComponent,
    } from '@vue/composition-api'
    import {
        extend,
        ValidationObserver,
        ValidationProvider
    } from 'vee-validate';
    import {
        ConvertCustomFieldIntoDynamicFormObj,
        ConvertDynamicFormObjToCustomFieldObj
    } from "@/assets/common/vue-common.js"
    import DataService from '../../services/DataService';
    import moment from 'moment'
import { contains } from 'jquery';
    extend('required', {
        validate(value) {
            console.log("checkk")
            return {
                required: true,
                valid: ['', null, undefined].indexOf(value) === -1
            };
    
        },
        message: 'The {_field_} is required'
        ///computesRequired: true
    });
    export default defineComponent({
       // name: "Manage Departemts",
        components: {
            ValidationObserver,
            ValidationProvider
        },
        props: {
            addApiUrl: {
                type: String
            },
            emitName: {
                type: String
            },
            moduleName: {
                type: String
            },
            subModule: {
                type: String
            },
            title: {
                type: String
            },
            recordId: {
                type: Number
            },
            addUserGuide:{
                type: String
            }
        },
    
        created: function () {
            let id = this.recordId ? this.recordId : 0
            this.getForm(id)
            if (this.moduleName == 'Review' && this.subModule == 'PerformanceReview'){
                 this.GetReviewTemplateData();               
            }   
            
            setTimeout(() => {
                this.getchecked();
            }, 2000);
                        
        },     
        mounted:function(){
            // this.$nextTick(() => {
            //      this.getchecked();
            // });
        },

        watch: {
            
            hoursDifference() {
                // const dynamicFormElement = this.$refs.dynamicForm.$el;
                // const shiftHoursInput = dynamicFormElement.querySelector('#shift_hours input[type="text"]');
                // if (shiftHoursInput) {
                //     shiftHoursInput.value = this.hoursDifference;
                // }
            },            
            
        },
        methods: {

            getchecked(){
            if (this.moduleName == 'Review' && this.subModule == 'PerformanceReview'){                
                if(this.skill != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_3')).prop('checked',true);
                 }

                 if(this.notes != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_0')).prop('checked',true);
                 }

                 if(this.feedback != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_1')).prop('checked',true);
                 }

                 if(this.goal != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_2')).prop('checked',true);
                 }

                 if(this.training != ''){                    
                    $('.input-group').find('.custom-checkbox').find($('input[type="checkbox"]#is_skill_enable_4')).prop('checked',true);
                 }
                 
              }
              
              if (this.moduleName == 'Immigration' && this.subModule == 'Passport'){  
                if(this.male != ''){                                       
                    $('.input-group').find('.custom-radio').find($('input[type="radio"]#sex_0')).prop('checked',true);
                 }
                 if(this.female != ''){                    
                    $('.input-group').find('.custom-radio').find($('input[type="radio"]#sex_1')).prop('checked',true);
                 }
              }
            },


            getDifference() {
                
                if (this.startTime && this.endTime) {
                    const startParts = this.startTime.split(':');
                    const endParts = this.endTime.split(':');

                    let startHour = parseInt(startParts[0]);
                    let endHour = parseInt(endParts[0]);
                        
                    // Convert PM times to 24-hour format
                    if (startParts[1].includes('PM') && startHour !== 12) {
                        startHour += 12;
                    }
                    if (endParts[1].includes('PM') && endHour !== 12) {
                        endHour += 12;
                    }
                    
                    const startMinutes = startHour * 60 + parseInt(startParts[1]);
                    const endMinutes = endHour * 60 + parseInt(endParts[1]);
                    
                    let differenceMinutes = endMinutes - startMinutes;
                    if (differenceMinutes < 0) {
                        differenceMinutes += 24 * 60; // Add 24 hours if end time is before start time
                    }
                    
                    let hours = Math.floor(differenceMinutes / 60);
                    let minutes = differenceMinutes % 60;

                    this.hoursDifference = parseFloat(hours + '.' + minutes);
                } else {
                // Return a message indicating that both start time and end time are required
                return 'Start time and end time are required to calculate the difference.';
                }
            },
            getForm: function (id) {
                var ths = this;
                ths.isLoading = true;
                var url = `recordId=${id}&moduleName=${this.moduleName}&subModuleCode=${this.subModule}`;
                DataService.GetDynamicForm(url).then(function (response) {
                    if (response != null && response.data != null) {
                        
                        ths.isMapFeature = response.data.isFsmLocating;
                        // response.data.data                    
                        ths.FormSchema = ths.$refs.dynamicForm.ConvertCustomFieldIntoDynamicFormObj(response.data.data);                        
                        if(ths.moduleName == 'Company' && ths.subModule == 'CompanyHoliday'){
                            var locationname = response.data.LocationData[0]["COMPANY_LOCATION_ID"];
                            ths.FormSchema.push({groupId:null,group_name:"",layoutType:'single',visibility:true,
                            Data:[
                                    {
                                        astype: "SlotField",
                                        label: '',
                                        name: "AvailableClientsLang",
                                        value: "",
                                        visibility: true,
                                        placeholder: "",
                                        config: {
                                            options: [],
                                            showAddIcon: false,
                                        },
                                        validationRules: "",
                                    }
                                ]
                            })

                            ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {                                
                            if(field.name == "Location"){
                                      field.value = locationname;
                                    }
                                });
                            });



                        }else if (ths.moduleName == 'Review' && ths.subModule == 'PerformanceReview'){
                            ths.FormSchema.push({groupId:null,group_name:"",layoutType:'single',visibility:true,
                            Data:[
                                    {
                                        astype: "SlotField",
                                        label: ths.$t('Question'),
                                        name: "ManageQuestionaire",
                                        value: "",
                                        visibility: true,
                                        placeholder: "",
                                        config: {
                                            options: [],
                                            showAddIcon: false,
                                        },
                                        validationRules: "",
                                    }
                                ]
                            })
                            ths.ManageQuestionaire = true;
                        }else if(ths.moduleName == 'Company' && ths.subModule == 'Feedback'){
                            ths.FormSchema.push({groupId:null,group_name:"",layoutType:'single',visibility:true,
                            Data:[
                                    {
                                        astype: "SlotField",
                                        label: "",
                                        name: "FeedbackQuestion",
                                        value: "",
                                        visibility: true,
                                        placeholder: "",
                                        config: {
                                            options: [],
