            async onSubmit() {
                var ths = this;
                var passportData = [];
                ths.isLoading = true;
                let fieldData = ths.$refs.dynamicForm.ConvertDynamicFormObjToCustomFieldObj(this.FormSchema)
                
                //  var attachmentData = this.fileData.length > 0 ? this.fileData : null;
                //  obj.push(attachmentData);

                if (this.moduleName == 'Break') {
                    //console.log("checkkkk",obj)
                    fieldData.find(function (entry) {
                        if (entry.field_name == 'break_duration') {
                            entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                        }
                    });
                }
                if (this.moduleName == 'Shift') {                                  
                    fieldData.find(function (entry) {
                        if (entry.field_name == 'start_time') {
                            entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                        }
                    });
                } 

                if (this.moduleName == 'Shift') {                                  
                    fieldData.find(function (entry) {
                        if (entry.field_name == 'end_time') {
                            entry.field_value = moment(new Date(entry.field_value)).format("HH:mm");
                        }
                    });
                } 
                
                if(ths.moduleName=='Immigration' && ths.subModule=='Passport'){
                    fieldData.find(function(entry){
                        if(entry.field_name != 'attachment'){
                            passportData.push(entry);
                        }
                    });
                fieldData = {
                        data: passportData,
                        moduleCode: ths.moduleName,
                        subModuleCode: ths.subModule,
                        attachmentData: ths.fileData.length > 0 ? ths.fileData : null
                        }  
                }   
                
                if(ths.moduleName=='AnnouncementMessage' && ths.subModule=='AnnouncementMessage'){

                fieldData = {
                        data: fieldData,
                        moduleCode: ths.moduleName,
                        subModuleCode: ths.subModule,
                        attachmentData: ths.fileData.length > 0 ? ths.fileData : null
                    }  
                }           
                else if (ths.moduleName == 'Company' && ths.subModule == 'Feedback')  {
                    fieldData = {
                        data: fieldData,
                        feedbackData: ths.FeedbackColumnArray
                    }
                }                              
                    
                DataService.DepartmentAdd(fieldData, this.addApiUrl).then((res) => {
                    ths.isLoading = false;
                    if (res.data.Result != null && res.data.Result.MAIN_ID > 0) {
                        this.ShowAlert(this.$t('SavedSuccessfully'), "success", false, this.$t("Alert"));
                        this.$emit(this.emitName)
                    } else {
                        //this.ShowAlert(this.$t(res.data.Status), "danger", false, this.$t("Alert"));
                        if(this.$t(res.data.Status) == 'FAILURE'){
                            this.ShowAlert(this.$t('ErrorWhileSavingRecords'), "danger", false, this.$t("Alert"));
                        }
                        else{
                            this.ShowAlert(this.$t('RecordAlreadyExists'), "danger", false, this.$t("Alert"));
                        }
                    }
    
                })
            },
