            GetAnswerType(QuestionType,feedbackindex){
                var vm = this;
                if(QuestionType == 'Objective'){
                    var checkexist = vm.FeedbackColumnArray.find(x=> x.FeedbackIndex === feedbackindex);
                    if(checkexist != undefined){
                        checkexist.FeedbackAnsweringType = "2-point scale (Eg: Yes, No)";
                        checkexist.FeedbackAnsweringValue = checkexist.FeedbackAnsweringType.split('(Eg: ')[1].replaceAll(', ','_').replaceAll(')','');
                    }
                }else{
                    vm.FeedbackColumnArray.forEach(item=>{
                        item.FeedbackAnsweringType = "",
                        item.FeedbackAnsweringValue = ''
                    })
                }
            },
            
            AddMoreFeedbackQuestion(){
                var vm = this;
                vm.FeedbackColumnArray.push({FeedbackQuestion:"",FeedbackAnswerType:"",FeedbackIndex:vm.count = vm.count + 1,FeedbackAnsweringType:'',FeedbackAnsweringValue:''});
            },

            DeleteFeedbackRow(index){
                var vm = this;
                var isExist = vm.FeedbackColumnArray.findIndex(x=> x.FeedbackIndex === index);
                if(isExist > -1){
                    vm.FeedbackColumnArray.splice(isExist,1);
                }
                vm.FeedbackColumnArray.forEach((item,index)=>{
                    item.FeedbackIndex = index;
                    vm.count = index;
                })
            },

            GetObjectiveValue(event,index){
                var vm = this;
                var checkexist = vm.FeedbackColumnArray.find(x=> x.FeedbackIndex === index);
                if(checkexist != undefined){
                    checkexist.FeedbackAnsweringType = event.target.value;
                    checkexist.FeedbackAnsweringValue = checkexist.FeedbackAnsweringType.split('(Eg: ')[1].replaceAll(', ','_').replaceAll(')','');
                }
            }
    
        },
        data() {
            return {
                startTime: null,
                endTime: null,
                hoursDifference: null,  
                fileData:[],              
                editor: this.CLASSIC_EDITOR,
                department: {
                    name: '',
                    status: {
                        name: "active",
                        id: 1
                    },
                    visibaleToclients: false,
                    visibaleToTicting: false,
                    description: '',
                },
                buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
                ],
                FormSchema: [],
                OtherSchema:[],
                isLoading: false,
                showContractDiv:false,
                ManageQuestionaire:false,
                modelStars:[],
                GetCheckedValue:null,   
                modelQuestionVisibility:[],
                GetReviewQuestionList:[],
                RadioOptions:[{label:'Enable',value:0},{label:'Disable',value:1}],
                RadioVisibilityOtions:[{label:'Both',value:0},{label:'Candidate',value:1},{label:'Reviewer',value:2}],
                FeedbackColumnArray:[{FeedbackQuestion:"",FeedbackAnswerType:"",FeedbackIndex:0,FeedbackAnsweringType:'',FeedbackAnsweringValue:''}],
                count:0,
                skill:'',
                notes:'',
                feedback:'',
                goal:'',
                training:'',
                male:'',
                female:''
            }
    
        },
    
    });
    </script>
    